import React, { useState, useEffect } from "react";
import { Button, TextField, Select, MenuItem, InputLabel, FormControl } from "@mui/material";
import * as XLSX from "xlsx";
import "./App.css"; // Ensure you have this CSS file

function App() {
  // State definitions
  const [suppliers, setSuppliers] = useState([]);
  const [transactions, setTransactions] = useState([]);
  const [data, setData] = useState([]); // State for the data.json file
  const [newSupplier, setNewSupplier] = useState("");
  const [selectedSupplier, setSelectedSupplier] = useState("");
  const [transaction, setTransaction] = useState({
    date: "",
    type: "Invoice",
    number: "",
    reference: "",
    amount: "",
    comments: "",
  });

  // Fetch suppliers, transactions, and data on component mount
  useEffect(() => {
    fetch("http://localhost:5000/suppliers")
      .then((res) => res.json())
      .then((data) => setSuppliers(data));

    // Fetch transactions from the GitHub JSON file
    fetch("https://raw.githubusercontent.com/LakmalRJ/accounting-app/main/transactions.json")
      .then((res) => res.json())
      .then((data) => setTransactions(data));

    // Fetch data from the GitHub JSON file
    fetch("https://raw.githubusercontent.com/LakmalRJ/accounting-app/main/data.json")
      .then((res) => res.json())
      .then((data) => setData(data));
  }, []);

  // Add a new supplier
  const addSupplier = () => {
    if (!newSupplier.trim()) return;
    const supplier = { name: newSupplier };

    fetch("http://localhost:5000/add-supplier", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(supplier),
    }).then(() => {
      setSuppliers([...suppliers, supplier]);
      setNewSupplier("");
    });
  };

  // Add a new transaction
  const addTransaction = () => {
    if (!selectedSupplier) return alert("Select a supplier first");
    if (!transaction.amount || isNaN(transaction.amount)) return alert("Enter a valid amount");

    // Check if an opening balance already exists for the supplier
    if (transaction.type === "Opening Balance") {
      const hasOpeningBalance = transactions.some(
        (t) => t.supplier === selectedSupplier && t.type === "Opening Balance"
      );
      if (hasOpeningBalance) {
        return alert("Only one Opening Balance is allowed per supplier.");
      }
    }

    const newTransaction = {
      ...transaction,
      _id: Date.now().toString(), // Add a unique ID (temporary, backend should handle this)
      supplier: selectedSupplier,
      amount: parseFloat(transaction.amount),
    };

    fetch("http://localhost:5000/add-transaction", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(newTransaction),
    }).then(() => {
      setTransactions([...transactions, newTransaction]);
      setTransaction({ date: "", type: "Invoice", number: "", reference: "", amount: "", comments: "" });
    });
  };

  // Delete a transaction
  const deleteTransaction = async (id) => {
    try {
      // Send a DELETE request to the backend
      const response = await fetch(`http://localhost:5000/delete-transaction/${id}`, {
        method: "DELETE",
      });

      if (response.ok) {
        // Update the frontend state to remove the deleted transaction
        setTransactions(transactions.filter((t) => t._id !== id));
      } else {
        alert("Failed to delete transaction.");
      }
    } catch (error) {
      console.error("Error deleting transaction:", error);
      alert("An error occurred while deleting the transaction.");
    }
  };

  // Filter transactions for the selected supplier
  const supplierTransactions = transactions.filter((t) => t.supplier === selectedSupplier);

  // Sort transactions by date (ascending)
  const sortedTransactions = supplierTransactions.sort((a, b) => new Date(a.date) - new Date(b.date));

  // Calculate the balance for the selected supplier
  const calculateBalance = () => {
    const openingBalanceTransaction = supplierTransactions.find(
      (t) => t.type === "Opening Balance"
    );
    const openingBalance = openingBalanceTransaction ? openingBalanceTransaction.amount : 0;

    return supplierTransactions
      .filter((t) => t.type !== "Opening Balance") // Exclude opening balance from the calculation
      .reduce((total, t) => {
        return t.type === "Invoice" ? total + t.amount : total - t.amount;
      }, openingBalance); // Start with the opening balance
  };

  // Format amount with commas and color coding (for app rendering only)
  const formatAmount = (amount, type) => {
    const formattedAmount = new Intl.NumberFormat("en-GB", {
      minimumFractionDigits: 2,
      maximumFractionDigits: 2,
    }).format(Math.abs(amount));

    if (type === "Payment" || type === "Credit Note" || (type === "Opening Balance" && amount < 0)) {
      return <span className="negative-amount">{formattedAmount}</span>;
    } else {
      return <span className="positive-amount">{formattedAmount}</span>;
    }
  };

  // Download all supplier ledgers as an Excel file
  const downloadExcel = () => {
    const wb = XLSX.utils.book_new();

    suppliers.forEach((supplier) => {
      // Filter transactions for the current supplier
      const supplierTransactions = transactions.filter((t) => t.supplier === supplier.name);

      // Find the opening balance (if any)
      const openingBalanceTransaction = supplierTransactions.find(
        (t) => t.type === "Opening Balance"
      );
      const openingBalance = openingBalanceTransaction ? openingBalanceTransaction.amount : 0;

      // Calculate the running balance and prepare the worksheet data
      let runningBalance = openingBalance; // Start with the opening balance
      const wsData = supplierTransactions
        .filter((t) => t.type !== "Opening Balance") // Exclude opening balance from the main list
        .map((t) => {
          runningBalance += t.type === "Invoice" ? t.amount : -t.amount;
          return {
            Date: t.date,
            "Document Type": t.type,
            "Document Number": t.number,
            Reference: t.reference,
            Amount: t.type === "Invoice" ? t.amount : -t.amount, // Use raw numbers for Excel
            "Running Balance": runningBalance, // Add running balance
            Comments: t.comments,
          };
        });

      // Add the opening balance as the first row (if it exists)
      if (openingBalanceTransaction) {
        wsData.unshift({
          Date: openingBalanceTransaction.date,
          "Document Type": "Opening Balance",
          "Document Number": "",
          Reference: "",
          Amount: openingBalance, // Opening balance as a number
          "Running Balance": openingBalance, // Running balance starts with opening balance
          Comments: openingBalanceTransaction.comments,
        });
      }

      // Add the closing balance as a final row
      wsData.push({
        Date: "",
        "Document Type": "Closing Balance",
        "Document Number": "",
        Reference: "",
        Amount: "",
        "Running Balance": runningBalance, // Closing balance
        Comments: "",
      });

      // Create the worksheet
      const ws = XLSX.utils.json_to_sheet(wsData);

      // Set the cell type for the "Amount" and "Running Balance" columns to 'n' (number)
      const range = XLSX.utils.decode_range(ws["!ref"]);
      for (let row = range.s.r + 1; row <= range.e.r; row++) {
        const amountCellAddress = XLSX.utils.encode_cell({ r: row, c: 4 }); // Column 4 is "Amount"
        const balanceCellAddress = XLSX.utils.encode_cell({ r: row, c: 5 }); // Column 5 is "Running Balance"
        if (ws[amountCellAddress]) {
          ws[amountCellAddress].t = "n"; // Set cell type to number
        }
        if (ws[balanceCellAddress]) {
          ws[balanceCellAddress].t = "n"; // Set cell type to number
        }
      }

      // Add the worksheet to the workbook with the supplier's name as the sheet name
      XLSX.utils.book_append_sheet(wb, ws, supplier.name);
    });

    // Write the file
    XLSX.writeFile(wb, `All_Supplier_Ledgers.xlsx`);
  };

  return (
    <div className="container">
      <h1 className="title">Supplier Management</h1>

      {/* Add Supplier */}
      <div className="section">
        <h2 className="subtitle">Add Supplier</h2>
        <div className="input-row">
          <TextField
            label="Supplier Name"
            variant="outlined"
            size="small"
            value={newSupplier}
            onChange={(e) => setNewSupplier(e.target.value)}
            fullWidth
          />
          <Button variant="contained" color="primary" onClick={addSupplier} size="small" sx={{ ml: 2 }}>
            Add
          </Button>
        </div>
      </div>

      {/* Select Supplier */}
      <div className="section">
        <FormControl fullWidth size="small">
          <InputLabel>Select Supplier</InputLabel>
          <Select
            value={selectedSupplier}
            onChange={(e) => setSelectedSupplier(e.target.value)}
            variant="outlined"
          >
            <MenuItem value="">Select Supplier</MenuItem>
            {suppliers.map((s, index) => (
              <MenuItem key={index} value={s.name}>
                {s.name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </div>

      {/* Add Transaction */}
      <div className="section">
        <h2 className="subtitle">Add Transaction</h2>
        <div className="input-row">
          <TextField
            label="Date"
            variant="outlined"
            size="small"
            type="date"
            value={transaction.date}
            onChange={(e) => setTransaction({ ...transaction, date: e.target.value })}
            InputLabelProps={{ shrink: true }}
            fullWidth
          />
          <FormControl variant="outlined" size="small" fullWidth sx={{ ml: 2 }}>
            <InputLabel>Type</InputLabel>
            <Select
              value={transaction.type}
              onChange={(e) => setTransaction({ ...transaction, type: e.target.value })}
            >
              <MenuItem value="Invoice">Invoice</MenuItem>
              <MenuItem value="Payment">Payment</MenuItem>
              <MenuItem value="Credit Note">Credit Note</MenuItem>
              <MenuItem value="Opening Balance">Opening Balance</MenuItem>
            </Select>
          </FormControl>
        </div>
        <div className="input-row">
          <TextField
            label="Document Number"
            variant="outlined"
            size="small"
            value={transaction.number}
            onChange={(e) => setTransaction({ ...transaction, number: e.target.value })}
            fullWidth
          />
          <TextField
            label="Reference"
            variant="outlined"
            size="small"
            value={transaction.reference}
            onChange={(e) => setTransaction({ ...transaction, reference: e.target.value })}
            fullWidth
            sx={{ ml: 2 }}
          />
        </div>
        <div className="input-row">
          <TextField
            label="Amount"
            variant="outlined"
            size="small"
            type="number"
            value={transaction.amount}
            onChange={(e) => setTransaction({ ...transaction, amount: e.target.value })}
            fullWidth
          />
          <TextField
            label="Comments"
            variant="outlined"
            size="small"
            value={transaction.comments}
            onChange={(e) => setTransaction({ ...transaction, comments: e.target.value })}
            fullWidth
            sx={{ ml: 2 }}
          />
        </div>
        <Button variant="contained" color="primary" onClick={addTransaction} size="small" sx={{ mt: 2 }}>
          Add Transaction
        </Button>
      </div>

      {/* Transaction List */}
      <div className="section">
        <h2 className="subtitle">Transactions for {selectedSupplier}</h2>
        <table>
          <thead>
            <tr>
              <th>Date</th>
              <th>Type</th>
              <th>Number</th>
              <th>Reference</th>
              <th>Amount</th>
              <th>Comments</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {sortedTransactions.map((t) => (
              <tr key={t._id}>
                <td>{t.date}</td>
                <td>{t.type}</td>
                <td>{t.number}</td>
                <td>{t.reference}</td>
                <td>{formatAmount(t.amount, t.type)}</td>
                <td>{t.comments}</td>
                <td>
                  <Button variant="outlined" color="secondary" onClick={() => deleteTransaction(t._id)}>
                    Delete
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        <h3>
          Balance: {formatAmount(calculateBalance(), "Balance")}
        </h3>
      </div>

      {/* Download Button */}
      <Button variant="contained" color="primary" onClick={downloadExcel} size="small" sx={{ mt: 2 }}>
        Download All Supplier Ledgers
      </Button>
    </div>
  );
}

export default App;
